@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
    --bg-primary : #424242;
    --bg-primary-dark :#1b1b1b;
    --bg-primary-light: #6d6d6d;
    --bg-secondary : #9e9e9e;
    --bg-secondary-dark:#707070;
    --bg-secondary-light:#cfcfcf;
    --text-primary: #ffffff;
    --text-secondary: #000000;

    --font-family-body: 'Press Start 2P', cursive;
}
body {
    background-color: var(--bg-primary);
    color: var(--text-primary);
    font-family: var(--font-family-body);
}
header {
    padding: 50px 0;
    background: var(--bg-primary-dark);
    width: 100%;
}

header .wrap{
    width: 1200px;
    margin: 0 auto;
}

.title-area {
    display: inline-block;
    height: 40px;
    width: 450px;
    text-align: left;
    font-size: 20px;
    line-height: 50px;
}

nav {
    float: right;
}

nav li {
    display: inline-block;
    padding: 15px 15px;
}
.forest {
    height: 793px;
    background: url(../../static/media/Background.a6cbed8c.png);
}

.content {
    width: 1200px;
    margin: 0 auto;
}

.content p{
    margin: 30px 0;

}
footer {
    background: var(--bg-primary-dark);
    padding: 50px 0 40px;
}

.footer-text {
    text-align: center;
}

.footer-text p {
    font-size: 16px;
    font-weight: bold;
}

.footer-text li {
    display: inline-block;
    padding: 30px 20px;

}

.footer-text img {
    -webkit-filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(124deg) brightness(104%) contrast(101%);
            filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(124deg) brightness(104%) contrast(101%);
    width: 50px;
    height: 50px;
}
